import { OpenAI } from "openai";

const openai = new OpenAI({
  apiKey: process.env.OPENAI_API_KEY,
});

export const runtime = "edge";

export async function POST(req: Request) {
  try {
    const { messages } = await req.json();

    const response = await openai.chat.completions.create({
      model: "gpt-3.5-turbo",
      stream: true,
      messages: [
        {
          role: "system",
          content:
            "Vous êtes un assistant virtuel pour Romain KORDALSKI, un développeur web. Votre rôle est de répondre aux questions concernant ses compétences, son expérience et sa formation.",
        },
        ...messages,
      ],
    });

    // Créer un flux de lecture
    const stream = new ReadableStream({
      async start(controller) {
        for await (const chunk of response) {
          const text = chunk.choices[0]?.delta?.content || "";
          const encoder = new TextEncoder();
          controller.enqueue(encoder.encode(text)); // Envoie le texte en continu au client
        }
        controller.close();
      },
    });

    return new Response(stream, {
      headers: { "Content-Type": "text/event-stream" },
    });
  } catch (error) {
    console.error("Erreur dans le chatbot:", error);
    return new Response("Erreur interne du serveur", { status: 500 });
  }
}
